'use client';
import { useRouter } from 'next/navigation';
import { supabase } from '../../lib/supabaseClient';
import { useEffect, useState, ReactNode } from 'react';

// --- Reusable Dashboard Card Component ---
interface DashboardCardProps {
  title: string;
  description: string;
  icon: ReactNode;
  colorClass: string; // Tailwind CSS class for color theming (e.g., 'from-green-400 to-teal-400')
  href?: string;
}

const DashboardCard = ({ title, description, icon, colorClass, href }: DashboardCardProps) => {
  const router = useRouter();
  const handleClick = () => {
    if (href) {
      router.push(href);
    }
  };

  // Adicionado um estilo condicional para o cursor
  const cardClasses = `
    bg-white p-6 rounded-xl shadow-lg hover:shadow-2xl transition-all 
    duration-300 transform hover:-translate-y-2 group
    ${href ? 'cursor-pointer' : 'cursor-default'}
  `;

  return (
    <div className={cardClasses} onClick={handleClick}>
      <div className={`w-16 h-16 rounded-full bg-gradient-to-br ${colorClass} flex items-center justify-center mb-5 shadow-lg group-hover:scale-110 transition-transform`}>
        {icon}
      </div>
      <h3 className="text-2xl font-bold text-gray-800 mb-2">{title}</h3>
      <p className="text-gray-600">{description}</p>
    </div>
  );
};


// --- Main Home Page Component ---
interface UserProfile {
  id: string;
  nome_completo?: string;
  email?: string;
  role?: string;
}

export default function HomePage() {
  const router = useRouter();
  const [userProfile, setUserProfile] = useState<UserProfile | null>(null);
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    const fetchUserData = async () => {
      setLoading(true);
      const { data: { user } } = await supabase.auth.getUser();
      if (!user) { router.push('/login'); return; }

      const [profileRes, roleRes] = await Promise.all([
        supabase.from('usuarios').select('nome_completo').eq('id', user.id).maybeSingle(),
        supabase.from('user_roles').select('role').eq('user_id', user.id).maybeSingle()
      ]);

      setUserProfile({
        id: user.id,
        email: user.email,
        nome_completo: profileRes.data?.nome_completo,
        role: roleRes.data?.role || 'USER',
      });
      setLoading(false);
    };
    fetchUserData();
  }, [router]);

  const handleLogout = async () => {
    await supabase.auth.signOut();
    router.push('/login');
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen bg-slate-50">
        <div className="text-xl font-semibold text-gray-700 animate-pulse">Carregando sua jornada...</div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-slate-50 text-gray-800">
      <header className="bg-white/80 backdrop-blur-md shadow-sm sticky top-0 z-50">
        <nav className="container mx-auto px-6 py-4 flex justify-between items-center">
          <div 
            className="flex items-center gap-3 cursor-pointer" 
            onClick={() => router.push('/home')}
          >
            <img src="/imgs/travel_1000dp_FFF_FILL0_wght400_GRAD0_opsz48.svg" alt="Jacarelli Viagens Logo" className="h-8 w-8 bg-green-500 rounded-full p-1" />
            <span className="text-2xl font-bold text-green-500">Jacarelli Viagens</span>
          </div>
          <div className="flex items-center gap-4">
            <span className="font-medium hidden sm:block">
              Olá, {userProfile?.nome_completo?.split(' ')[0] || 'Viajante'}!
            </span>
            <button
              onClick={handleLogout}
              className="flex items-center gap-2 py-2 px-4 bg-red-500 text-white font-semibold rounded-lg shadow-md hover:bg-red-600 transition-all duration-300 transform hover:scale-105 cursor-pointer"
            >
              <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor"><path fillRule="evenodd" d="M3 3a1 1 0 00-1 1v12a1 1 0 102 0V4a1 1 0 00-1-1zm10.293 9.293a1 1 0 001.414 1.414l3-3a1 1 0 000-1.414l-3-3a1 1 0 10-1.414 1.414L14.586 9H7a1 1 0 100 2h7.586l-1.293 1.293z" clipRule="evenodd" /></svg>
              Sair
            </button>
          </div>
        </nav>
      </header>

      <main className="container mx-auto px-6 py-12">
        <div className="text-center mb-16">
          <h1 className="text-5xl md:text-6xl font-extrabold text-gray-900 mb-4">Sua Aventura Começa Aqui!</h1>
          <p className="text-lg text-gray-600 max-w-3xl mx-auto">
            Bem-vindo ao seu painel. Explore os destinos mais incríveis, gerencie suas viagens e acesse seu perfil.
          </p>
        </div>
        
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          <DashboardCard
            title="Pacotes Imperdíveis"
            description="Descubra nossas ofertas e planeje a viagem dos seus sonhos com os melhores preços."
            colorClass="from-green-400 to-teal-500"
            href="/packages"
            icon={<svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 8h14M5 8a2 2 0 110-4h14a2 2 0 110 4M5 8v10a2 2 0 002 2h10a2 2 0 002-2V8m-9 4h4" /></svg>}
          />
          <DashboardCard
            title="Minhas Viagens"
            description="Acesse seu histórico de compras, reservas e detalhes dos seus pacotes."
            colorClass="from-blue-400 to-indigo-500"
            href="/my-trips" // Conectado à nova página
            icon={<svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2m-3 7h3m-3 4h3m-6-4h.01M9 16h.01" /></svg>}
          />
          <DashboardCard
            title="Meu Perfil"
            description="Atualize seus dados cadastrais, preferências e informações de contato."
            colorClass="from-purple-400 to-pink-500"
            href="/profile" // Conectado à nova página
            icon={<svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" /></svg>}
          />
        </div>

        {userProfile?.role === 'ADMIN' && (
          <div className="mt-16 bg-gradient-to-r from-gray-800 to-gray-900 text-white p-10 rounded-xl shadow-2xl text-left">
            <h2 className="text-4xl font-bold mb-4">Área do Administrador</h2>
            <p className="mb-6 opacity-80 max-w-2xl">Acesso exclusivo para gerenciar usuários, cadastrar pacotes de viagem e visualizar relatórios da plataforma.</p>
            <button
              onClick={() => router.push('/admin')}
              className="py-3 px-8 bg-green-500 text-white font-bold rounded-lg shadow-lg hover:bg-green-600 transition-all duration-300 transform hover:scale-105 cursor-pointer"
            >
              Acessar Painel de Controle
            </button>
          </div>
        )}
      </main>
    </div>
  );
}
